import Vue from 'vue'
import Router from 'vue-router'

import ErrorPage from '@/components/ErrorPage'
import LoadingPage from '@/components/LoadingPage'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: __dirname,
  routes: [
    {
      path: '/a/b/c',
      name: 'ErrorPage',
      component: ErrorPage
    },{
      path: '/a/b/d',
      name: 'LoadingPage',
      component: LoadingPage
    }
  ]
})



